/* src/styles/Timeline.css */
.timeline-container {
  padding: 4rem 2rem;
  background-color: #fff0f5;
  text-align: center;
}

.timeline-container h2 {
  font-size: 2rem;
  color: #d63384;
  margin-bottom: 2rem;
}

.timeline {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.timeline-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffe6f0;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.timeline-img {
  width: 100%;
  max-width: 300px;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.timeline-content h3 {
  margin: 0;
  color: #b03060;
}

.timeline-date {
  font-size: 0.9rem;
  color: #666;
  margin: 0.5rem 0;
}

.timeline-content p {
  font-size: 1rem;
  color: #333;
}
