/* src/components/HeroSection.css */
.hero-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem;
  background: linear-gradient(to bottom right, #ffd6ec, #ffe6f0);
}

.hero-section h1 {
  font-size: 2.5rem;
  color: #d63384;
  margin-bottom: 1rem;
}

.hero-section p {
  font-size: 1.2rem;
  color: #444;
  margin-bottom: 2rem;
  max-width: 500px;
}

.hero-section button {
  background-color: #ff69b4;
  border: none;
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.hero-section button:hover {
  background-color: #e0559e;
}

.music-btn {
  margin-top: 1rem;
  background-color: #fff;
  color: #d63384;
  border: 2px solid #d63384;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.music-btn:hover {
  background-color: #d63384;
  color: white;
}
