/* src/styles/Countdown.css */
.countdown-container {
  background-color: #fffbe6;
  padding: 4rem 2rem;
  text-align: center;
}

.countdown-container h2 {
  color: #cc6600;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.countdown-container p {
  font-size: 1.5rem;
  color: #333;
  margin: 0;
}

.countdown-note {
  display: block;
  margin-top: 1rem;
  font-size: 1rem;
  color: #666;
}
